@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@600&display=swap');

.conQuesText{
    border-bottom: 1px solid #000;
 
   
}
.quesText{
    color: #2A292A;

    font-family: Noto Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

}
.tittleTextQues{
    width: 1000px;
}
.btnitem{
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    justify-content: flex-start;
    
}

.btnQuest {
    border: 1px solid 2A292A;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.14);
    padding: 15px 18px;
    outline: none;
    border-radius: 10px;
    color: #000;
    width: 344px;
    text-align: center;
    background: #FAF9FA;
    font-family: Noto Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
   cursor: pointer;
   transition: border-color 0.3s, background-color 0.3s;
   
}
.btnQuest:hover {
    border: 2px solid #2A292A;


    
}

.listBtnItem:nth-child(-n+2){
    margin-bottom: 20px;

}
.listBtnItem:nth-child(1),
.listBtnItem:nth-child(3) {
    margin-right: 25px;
}

.correctAnswer {
    border: 2px solid green;
    box-shadow: 0px 0px 18px 0px rgba(0, 128, 0, 0.5);
}

.wrongAnswer {
    border: 2px solid red;
    box-shadow: 0px 0px 18px 0px rgba(255, 0, 0, 0.5);
}

.defaultButton {
    border: 1px solid #2A292A;
}

.nextQuestionBtn{
    border-radius: 6px;
    border: 2px solid #425F9C;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #1A2B4F;
    border: none;
    color: #ffff;
    padding: 15px 35px;
    width: 140px;
    
    cursor: pointer;
    width: 140px;

}
.containerQuestion{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    
}

.conQuestionImg{
    display: flex;
    margin-left: 20px;
    margin-top: 30px;
}

.conItem{
    display: flex;
    flex-direction: column;

}

.nextbtn{
    display: flex;
    justify-content: flex-end;
    margin-top: 190px;
    margin-right: 30px;
}

.imgcon{
    margin-right: 30px;
}
.conQuesTextAndNumb{

    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    margin-left: 20px;
    margin-right: 20px;
}

.currentQuestion{
    color: #2A292A;

    text-align: center;
    font-family: Noto Sans;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}